import React, { Component } from 'react';
import Popover from 'react-popover';
import ColorOption from './components/ColorOption/';
import SelectOption from './components/SelectOption/index';
import VisibleOption from './components/VisibleOption/';
import TextureOption from './components/TextureOption/';
import Viewer from './components/Viewer/';
import availableOptions from './availableOptions';
import './App.css';

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            model: null,
            materials: [],
            scene: null,
            isAddOptionOpen: false,
            options: [],
            lastClickEvent: null
        };
    }

    // componentDidMount() {
    //     this.setState({
    //         model: { uid: '58425893f23d4ee691014e43fa12980c' }
    //     });
    // }

    handleLoadModelClick() {
        var picker = new window.SketchfabPicker();
        picker.pick({
            success: model => {
                console.log(model);
                this.setState({
                    model
                });
            }
        });
    }

    serializeConfig() {
        let options = this.state.options.map(option => {
            return availableOptions[option.type].serializeOption(option);
        });
        const output = {
            model: this.state.model.uid,
            params: {
                camera: 0,
                preload: 1,
                ui_controls: 0,
                ui_infos: 0,
                ui_watermark: 0
            },
            config: options
        };

        const json = JSON.stringify(output, null, 4);
        console.log('Serialized', json);
        return json;
    }

    handleExportClick = e => {
        var serializedConfig = this.serializeConfig();
        const blob = new Blob([serializedConfig], {
            type: 'application/json;charset=utf-8'
        });
        var url = window.URL.createObjectURL(blob);
        var a = document.createElement('a');
        a.href = url;
        a.download = `${this.state.model.uid}.json`;
        a.click();
        window.URL.revokeObjectURL(url);
    };

    handlePreviewClick = e => {
        var serializedConfig = this.serializeConfig();
        const blob = new Blob([serializedConfig], {
            type: 'application/json;charset=utf-8'
        });
        const fileReader = new FileReader();
        fileReader.onload = function(e) {
            const configUrl = e.target.result;
            window.open(
                `https://demos.sketchfab.com/clients/configurator/index.html?config=${encodeURIComponent(
                    configUrl
                )}`,
                'configurator-preview'
            );
        };
        fileReader.readAsDataURL(blob);
    };

    onModelLoaded(materials, scene) {
        this.setState({
            materials,
            scene
        });
        console.log(scene);
    }

    onViewerClicked = e => {
        this.setState({
            lastClickEvent: {
                ...e,
                createdAt: Date.now()
            }
        });
    };

    updateOption = (uid, updatedOption) => {
        const newOptions = this.state.options.map(option => {
            if (option.uid === uid) {
                return updatedOption;
            }
            return option;
        });
        this.setState({
            options: newOptions
        });
    };

    addOption(optionType) {
        var newOption = availableOptions[optionType].defaultState();
        this.setState({
            options: [...this.state.options, newOption],
            isAddOptionOpen: false
        });
    }

    deleteOption = uid => {
        var index = this.state.options.reduce((acc, cur, index) => {
            if (cur.uid === uid) {
                return index;
            }
            return acc;
        }, null);

        if (index !== null) {
            let options = Array.from(this.state.options);
            options.splice(index, 1);
            this.setState({
                options: options
            });
        }
    };

    renderOptions() {
        return this.state.options.map((option, i) => {
            switch (option.type) {
                case 'color':
                    return (
                        <ColorOption
                            option={option}
                            materials={this.state.materials}
                            key={option.uid}
                            onDeleteOption={this.deleteOption}
                            onUpdateOption={this.updateOption}
                            lastClickEvent={this.state.lastClickEvent}
                        />
                    );
                case 'visible':
                    return (
                        <VisibleOption
                            option={option}
                            key={option.uid}
                            onDeleteOption={this.deleteOption}
                            onUpdateOption={this.updateOption}
                            lastClickEvent={this.state.lastClickEvent}
                            scene={this.state.scene}
                        />
                    );
                case 'select':
                    return (
                        <SelectOption
                            option={option}
                            key={option.uid}
                            onDeleteOption={this.deleteOption}
                            onUpdateOption={this.updateOption}
                            lastClickEvent={this.state.lastClickEvent}
                            scene={this.state.scene}
                        />
                    );
                case 'texture':
                    return (
                        <TextureOption
                            option={option}
                            key={option.uid}
                            onDeleteOption={this.deleteOption}
                            onUpdateOption={this.updateOption}
                            lastClickEvent={this.state.lastClickEvent}
                            materials={this.state.materials}
                        />
                    );
                default:
                    return null;
            }
        });
    }

    toggleAddOption = () => {
        this.setState({
            isAddOptionOpen: !this.state.isAddOptionOpen
        });
    };

    renderAvailableOptions = () => {
        const availableOptionKeys = Object.keys(availableOptions);
        const items = availableOptionKeys.map(availableOptionKey => {
            return (
                <li
                    key={availableOptionKey}
                    title={availableOptions[availableOptionKey].description}
                    onClick={() => {
                        this.addOption(availableOptionKey);
                    }}
                >
                    {availableOptions[availableOptionKey].label}
                </li>
            );
        });
        return <ul className="availableOptions">{items}</ul>;
    };

    render() {

        const viewerReady = this.state.model && this.state.materials && this.state.scene;

        return (
            <div className="App">
                <div className="header">
                    <div className="header__title">Configurator Studio</div>
                    <div className="header__actions">
                        <button className="button" onClick={this.handleLoadModelClick.bind(this)}>
                            Load model
                        </button>
                        <button
                            className="button"
                            onClick={this.handlePreviewClick}
                            disabled={viewerReady ? null : 'disabled'}
                        >
                            Preview
                        </button>
                        <button
                            className="button"
                            onClick={this.handleExportClick}
                            disabled={viewerReady ? null : 'disabled'}
                        >
                            Export
                        </button>
                    </div>
                </div>
                <div className="body">
                    <div className="viewer">
                        <Viewer
                            urlid={this.state.model ? this.state.model.uid : null}
                            onModelLoaded={this.onModelLoaded.bind(this)}
                            onViewerClicked={this.onViewerClicked}
                        />
                    </div>
                    <aside className="sidebar">
                        <div className="toolbar" />
                        <div className="add-option">
                            <Popover
                                isOpen={this.state.isAddOptionOpen}
                                body={this.renderAvailableOptions()}
                                onOuterAction={this.toggleAddOption}
                                enterExitTransitionDurationMs={0}
                                preferPlace="below"
                            >
                                <button
                                    className="button --primary"
                                    disabled={
                                        viewerReady ? null : 'disabled'
                                    }
                                    onClick={this.toggleAddOption}
                                >
                                    + Add option
                                </button>
                            </Popover>
                        </div>
                        <div className="options-list">{this.renderOptions()}</div>
                    </aside>
                </div>
            </div>
        );
    }
}

export default App;



